
html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
}

.App {
  text-align: center;
  width: 1600px;
  height: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #FFFFFF;
}

.App-header {
  background-color: #FFFFFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #FFFFFF;
  color: white;
  border-bottom: 1px solid #344b8e;
}

.App-title {
  font-size: 3.5rem;
  margin: 0;
  padding-left: 1rem;
  color: #ffa51f;
  text-shadow: 2px 2px 4px #344b8e;
}

.App-nav {
  margin-top: -580px;
  padding-left: 4rem;
  align-self: flex-start;
  display: flex;
  justify-content: flex-start;
}

.App-link {
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: white;
  background-color: #ffa51f;
  margin-left: -1.8rem;
  border: none;
  border-radius: 4px;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.App-link:hover {
  background-color: #ffd89e;
  color: #282c34;
  text-decoration: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
